   Avalilable for use under the terms of the MIT license. */
/* Paragraphs */
p {
  display: block;
  box-sizing: border-box;
  margin: 0;
  padding: 0.5rem 0;
}
/* Standard Lists */
ul,
ol {
  padding: 0;
  margin: 0 0 0.75em 25px;
}
ul ul,
ul ol,
ol ul,
ol ol {
  margin-bottom: 0;
}
li {
  line-height: 1.5em;
}
li h1,
li h2,
li h3,
li h4,
li h5 {
  padding-bottom: 0;
}
/* Definition Lists */
dl {
  margin-bottom: 1.5em;
}
dt,
dd {
  line-height: 1.5em;
}
dt {
  font-weight: bold;
}
dd {
  margin-left: 0.75em;
}
/* Horizontal Rule */
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  margin: 1.5em 0;
  border: 0;
  border-top: 1px dotted #cccccc;
  border-bottom: 1px dotted #ffffff;
}
/* Quotes */
blockquote {
  border-left-style: solid;
  border-left-width: 1px;
  border-color: #408bc9;
  border-width: 0.25rem;
  font-style: normal;
  line-height: 1.5;
  color: #444;
  background-color: #eee;
  padding: 0.3rem 2rem;
  margin: 0;
  font-size: 1rem;
}
blockquote:before,
blockquote:after {
  content: "";
}
blockquote p {
  margin-bottom: 0;
  font-size: 1em;
  line-height: 1.5em;
}
blockquote small {
  display: block;
  line-height: 1.5em;
  color: #999;
}
blockquote small:before {
  content: '\2014 \00A0';
}
@media screen and (max-width: 60em) {
  blockquote {
    padding: 0.1rem 1rem;
  }
}
q:before {
  content: "\201C";
}
q:after {
  content: "\201D";
}
/* Other */
details {
  display: block;
}
details div.details {
  padding: 0 0 0 2rem;
}
summary {
  display: list-item;
  cursor: pointer;
}
template {
  display: none;
}
